/* ====================================================== 
   <!-- Sidebar  -->            
/* ====================================================== */
/*
 * 1. Basic                             - Synchronize to WordPress's class name
 * 2. Calendar Widget                   - Synchronize to WordPress's class name
 * 3. Link List                         - Synchronize to WordPress's class name
 * 4. Tag cloud from sidebar            - Synchronize to WordPress's class name
 * 5. Post List
 * 6. Post List with Featured Image
*/
/* 
 ---------------------------
 1. Basic
 ---------------------------
 */
.uix-sidebar__container {
  position: relative;
  word-wrap: break-word;
  padding-left: 2.1875rem;
  line-height: 1.825;
}

.uix-sidebar__container .widget {
  padding-bottom: 2.625rem;
  font-size: 0.875rem;
}

.uix-sidebar__container .widget.js-uix-sticky-el.is-active {
  background: #fff;
  padding-top: 10px;
}

.uix-sidebar__container .widget-title {
  margin: 0 0 0.875rem;
}

@media all and (max-width: 768px) {
  .uix-sidebar__container {
    padding-left: 0;
  }
}

/* 
 ---------------------------
 2. Calendar Widget
 ---------------------------
 */
.calendar_wrap > table {
  padding: 1.3125rem 0;
  width: 100%;
  font-size: 0.5625rem;
}

.calendar_wrap > table caption {
  text-align: center;
  margin: 0.875rem 0;
  font-size: 1.25rem;
}

.calendar_wrap > table thead th {
  padding-bottom: 0.625rem;
  text-align: center;
}

.calendar_wrap > table tbody td {
  background: #f5f5f5;
  border: 1px solid #fff;
  text-align: center;
  padding: 0.5rem;
}

.calendar_wrap > table tbody td:hover {
  background: #fff;
}

.calendar_wrap > table tbody .pad {
  background: none;
}

.calendar_wrap > table tfoot #next {
  text-transform: uppercase;
  text-align: right;
}

.calendar_wrap > table tfoot #prev {
  text-transform: uppercase;
  padding-top: 0.875rem;
}

/* 
 ---------------------------
 3. Link List
 ---------------------------
 */
.widget_categories ul,
.widget_nav_menu ul,
.widget_pages ul {
  margin-left: 1.25rem;
}

.widget_categories ul li,
.widget_nav_menu ul li,
.widget_pages ul li {
  margin-bottom: 0;
  position: relative;
}

.widget_categories ul li a,
.widget_nav_menu ul li a,
.widget_pages ul li a {
  display: inline-block;
  padding: 0.35546875rem;
  font-style: italic;
  transition: 0.2s linear;
}

.widget_categories ul li a::before,
.widget_nav_menu ul li a::before,
.widget_pages ul li a::before {
  content: '\203A';
  opacity: 0.7;
  margin-right: 0.721875rem;
}

.widget_categories ul li > .cat-item-count,
.widget_nav_menu ul li > .cat-item-count,
.widget_pages ul li > .cat-item-count {
  position: absolute;
  top: 0.875rem;
  right: 0;
  border: 1px solid #C2C2C2;
  border-radius: 50px;
  display: inline-block;
  min-width: 16px;
  height: 16px;
  font-size: 0.625rem;
  text-align: center;
  line-height: 16px;
  opacity: 0.5;
}

.widget_categories ul > li,
.widget_nav_menu ul > li,
.widget_pages ul > li {
  border-bottom: 1px dashed #E6E6E6;
}

.widget_categories ul > li li,
.widget_nav_menu ul > li li,
.widget_pages ul > li li {
  border-bottom: none;
}

.widget_categories ul.children li:last-child,
.widget_nav_menu ul.children li:last-child,
.widget_pages ul.children li:last-child {
  border-bottom: none;
}

/* 
 ---------------------------
 4. Tag cloud from sidebar
 ---------------------------
 */
.tagcloud {
  margin-bottom: 1.3125rem;
}

.tagcloud > a {
  font-size: 0.875rem;
  padding: 0.04375rem 0.109375rem;
  margin-bottom: 0.175rem;
  display: inline-block;
}

.tagcloud > a::after {
  content: '/';
  display: inline-block;
  margin-left: 0.546875rem;
  opacity: 0.5;
}

.tagcloud > a:last-child:after {
  display: none;
}

.tagcloud.uix-tags-box--has-border > a {
  border: 1px solid #E6E6E6;
}

/* 
 ---------------------------
 5. Post List
 ---------------------------
 */
.uix-sidebar__posts li {
  border-bottom: 1px dashed #E6E6E6;
  margin-bottom: 0.721875rem;
}

/* 
 ---------------------------
 6. Post List with Featured Image
 ---------------------------
 */
.uix-sidebar__posts-img {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 1.3;
}

.uix-sidebar__posts-img li {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 1.3;
  border: none;
  display: block;
  margin-bottom: 0.875rem;
  display: block;
  height: auto;
  position: relative;
}

.uix-sidebar__posts-img li::after {
  display: block;
  content: '';
  clear: both;
  border-bottom: 1px dashed #E6E6E6;
}

.uix-sidebar__posts-img li .uix-sidebar__posts-img__thumb {
  float: left;
  width: 39.4%;
  margin-right: 0.875rem;
  margin-bottom: 0.875rem;
  padding: 0.125rem;
  display: block;
  text-align: left;
}

.uix-sidebar__posts-img li .uix-sidebar__posts-img__thumb img {
  margin: 0;
  padding: 0;
  max-width: 100%;
}

.uix-sidebar__posts-img li .uix-sidebar__posts-img__desc {
  float: left;
  width: 50%;
}

.uix-sidebar__posts-img li .uix-sidebar__posts-img__desc.no-image {
  float: none;
  width: 100%;
}

.uix-sidebar__posts-img li.uix-sidebar__posts-img__no-thumb {
  display: none;
}

.uix-sidebar__posts-img li .uix-sidebar__posts-img__date {
  opacity: 0.65;
  font-size: 0.75rem;
  padding: 0.6125rem 0;
}

@media all and (max-width: 991px) {
  .uix-sidebar__posts-img li .uix-sidebar__posts-img__thumb {
    width: 15%;
  }
}

@media all and (max-width: 480px) {
  .uix-sidebar__posts-img li .uix-sidebar__posts-img__thumb {
    width: 39.4%;
  }
}

@media all and (max-width: 991px) {
  .uix-sidebar__posts-img li .uix-sidebar__posts-img__desc {
    width: 82.5%;
  }
}

@media all and (max-width: 480px) {
  .uix-sidebar__posts-img li .uix-sidebar__posts-img__desc {
    width: 50%;
  }
}

/* ====================================================== 
   <!-- Social Media Icons -->
/* ====================================================== */
/*
 * 1. Social Button
 * 2. Social Background
*/
/* 
 ---------------------------
 1. Social Button
 ---------------------------
 */
.uix-social-btn {
  position: relative;
  border: 2px solid #212121;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  z-index: 1;
  color: #212121;
  padding: 0;
  margin: 0;
  /* white style */
}

.uix-social-btn.is-fill-white {
  background: #fff;
  color: #212121;
}

.uix-social-btn.uix-social-btn--no-border {
  border-color: transparent !important;
}

.uix-social-btn.uix-social-btn--thin {
  border-width: 1px;
}

.uix-social-btn.uix-social-btn--circle {
  border-radius: 50%;
}

.uix-social-btn.uix-social-btn--circle::after {
  border-radius: 50%;
}

.uix-social-btn.uix-social-btn--rounded {
  border-radius: 4px;
}

.uix-social-btn.uix-social-btn--rounded::after {
  border-radius: 4px;
}

.uix-social-btn.uix-social-btn--small {
  width: 2rem;
  height: 2rem;
  line-height: 1.875rem;
  margin-right: .5rem;
}

.uix-social-btn.uix-social-btn--small i {
  font-size: 1rem;
}

.uix-social-btn.uix-social-btn--medium {
  width: 2.875rem;
  height: 2.875rem;
  line-height: 2.875rem;
  margin-right: .75rem;
}

.uix-social-btn.uix-social-btn--medium i {
  font-size: 1.2rem;
}

.uix-social-btn.uix-social-btn--large {
  width: 3.75rem;
  height: 3.75rem;
  line-height: 3.75rem;
  margin-right: 1rem;
}

.uix-social-btn.uix-social-btn--large i {
  font-size: 1.5rem;
}

.uix-social-btn::after {
  transition: all 0.3s ease;
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  display: block;
  left: 0;
  top: 0;
  background-color: #212121;
  visibility: hidden;
  opacity: 0;
  transform: scale(0.5, 0.5);
  z-index: -1;
}

.uix-social-btn:hover i {
  color: #fff;
}

.uix-social-btn:hover::after {
  visibility: visible;
  opacity: 1;
  transform: scale(1, 1);
}

.uix-social-btn.uix-social-btn--white {
  border-color: #fff;
  color: #fff;
}

.uix-social-btn.uix-social-btn--white:hover i {
  color: #212121;
}

.uix-social-btn.uix-social-btn--white:hover::after {
  background-color: #fff;
}

