/* ====================================================== 
   <!-- Spacing -->                                
/* ====================================================== */
.uix-spacing--s {
  position: relative;
  padding: 3rem 0;
}

.uix-spacing--m {
  position: relative;
  padding: 6.5625rem 0;
}

.uix-spacing--l {
  position: relative;
  padding: 11.375rem 0;
}

.uix-spacing--no-top {
  padding-top: 0 !important;
}

.uix-spacing--no-bottom {
  padding-bottom: 0 !important;
}

.uix-spacing--googlemap {
  padding-top: 0 !important;
}

/* Without spacing */
.uix-spacing--no {
  padding: 0 !important;
  margin: 0 !important;
  /* compatible with <p>, <h1>, <h2>, <h3>... */
}

@media all and (max-width: 768px) {
  .uix-spacing--s {
    padding: 1.5rem 0;
  }
  .uix-spacing--m {
    padding: 3.28125rem 0;
  }
  .uix-spacing--l {
    padding: 5.6875rem 0;
  }
  .uix-spacing--no-top {
    padding-top: 0;
  }
  .uix-spacing--no-bottom {
    padding-bottom: 0;
  }
  .uix-spacing--googlemap {
    padding-top: 0;
  }
  .uix-spacing--no {
    padding: 0;
  }
}

/* ====================================================== 
   <!-- Tags -->      
/* ====================================================== */
.uix-tags-box::before {
  content: '';
  display: block;
  clear: both;
}

.uix-tags-box > a {
  font-size: 0.875rem;
  padding: 0.04375rem 0.109375rem;
  margin-bottom: 0.175rem;
  display: inline-block;
}

.uix-tags-box > a::after {
  content: '/';
  display: inline-block;
  margin-left: 0.546875rem;
  opacity: 0.5;
}

.uix-tags-box > a:last-child:after {
  display: none;
}

.uix-tags-box.uix-tags-box--has-border > a {
  border: 1px solid #E6E6E6;
}

/* ====================================================== 
   <!-- Typography -->            
/* ====================================================== */
/*
 * 1. General                    
 * 2. Hyperlink                        
 * 3. Heading (h1,h2,h3,h4,h5,h6) 
 * 4. Text Effect
 * 5. Drop Cap
 * 6. Vertical Text
*/
/* 
 ---------------------------
 1. General
 ---------------------------
 */
a,
h1,
h2,
h3,
h4,
h5,
h6,
.uix-typo--h1,
.uix-typo--h2,
.uix-typo--h3,
.uix-typo--h4,
.uix-typo--h5,
.uix-typo--h6 {
  transition: all 0.3s ease;
}

/* 
 ---------------------------
 2. Hyperlink
 ---------------------------
 */
a {
  color: #474747;
  text-decoration: none;
}

a:hover {
  color: #000;
  text-decoration: none;
}

/* 
 ---------------------------
 3. Heading (h1,h2,h3,h4,h5,h6)
 ---------------------------
 */
h1,
h2,
h3,
h4,
h5,
h6,
.uix-typo--h1,
.uix-typo--h2,
.uix-typo--h3,
.uix-typo--h4,
.uix-typo--h5,
.uix-typo--h6 {
  color: #212121;
  font-weight: 700;
  font-variant-ligatures: common-ligatures;
  /*Ligatures and contextual forms are ways of combining glyphs to produce more harmonized forms.*/
  margin-top: 0;
  margin-bottom: 0.625rem;
  padding: 0 0 0.546875rem;
  word-wrap: break-word;
}

h1 {
  font-size: 3.5rem;
  line-height: 1.14285714286;
}

h2 {
  font-size: 2.5rem;
  line-height: 1.2;
}

h3 {
  font-size: 1.75rem;
  line-height: 1.25;
}

h4 {
  font-size: 1.25rem;
  line-height: 1.33333333333;
}

h5 {
  font-size: 1rem;
  line-height: 1.33333333333;
  font-weight: 600;
}

h6 {
  font-size: 0.75rem;
  line-height: 1.5;
  font-weight: 600;
}

.uix-typo--h1 {
  font-size: 3.5rem !important;
  line-height: 1.14285714286 !important;
}

.uix-typo--h2 {
  font-size: 2.5rem !important;
  line-height: 1.2 !important;
}

.uix-typo--h3 {
