  line-height: 1.33333333333 !important;
  font-weight: 600 !important;
}

.uix-typo--h6 {
  font-size: 0.75rem !important;
  line-height: 1.5 !important;
  font-weight: 600 !important;
}

@media all and (max-width: 768px) {
  h1 {
    font-size: 2.5rem;
    line-height: 1.15555556;
  }
